<template>
    <div :class="[
    $namespace +'-search',
    isMargin?'search-margin':'',
    select? 'search-select':''
    ]">
        <JCSelect v-model="selectValue" placeholder="类型" :size="'medium'" v-if="select">
            <JCOption v-for="item in selectOptions" :key="item.value" :label="item.label" :value="item.value">
            </JCOption>
        </JCSelect>
        <div  :class="['search-'+type]" v-show="!placeholderMiddle">
            <i :class="[
                    'fa jcm-fangdajing',
                    select?'position-r':''
                    ] "></i>
            <form action="">
                <input ref="input" @click="visible = true" type="search" v-model="currentValue" :placeholder="placeholder" class="search-circle-input">
            </form>
            <i class="fa jcm-shanchu1" v-if="!select"  @click="currentValue = ''"></i>
        </div>
        <div  :class="['search-'+type,'search-text']" @click="placeholderMiddleClick" v-show="placeholderMiddle">
            <div class="search-show">
                <i class="jcm-fangdajing"></i>
                <span>搜索</span>
            </div>
        </div>
        <div :class="[
                    'search-cancel',
                    cancelTextDark? 'search-cancel-dark':'']" v-if="visible&&cancelText">
            <a href="#" @click="visible = false, currentValue = ''" v-text="cancelText"></a>
        </div>
    </div>
</template>
<script>
    export default {
        name: "JCSearch",
        props: {
            isPlaceholderMiddle:Boolean,//水印是否水平居中显示
            value: String,
            autofocus: Boolean,//自动聚焦
            isMargin:{//是否有左右边距
              type:Boolean,
              default:true
            },
            type: { //显示搜索框的类型，默认方形
                type: String,
                default: 'square'
            },
            cancelText: {//取消按钮文字
                default: '取消'
            },
            cancelTextDark: {//取消按钮文字是否为暗色调
                type: Boolean,
                default: true
            },
            placeholder: {//水印
                default: '搜索'
            },
            select: { //是否有类型过滤
                type: Boolean,
                default: false
            },
            selectOptions:{//过滤类型数据
              type:  Array,
                default:() => []
            },
            result: Array
        },
        data() {
            return {
                visible: false,
                currentValue: this.value,
                selectValue:''
            };
        },
        mounted() {
            this.autofocus && this.$refs.input.focus();
        },
        watch: {
            currentValue(val) {
                this.$emit('input', val);
                if(!val&&!this.cancelText)
                    this.visible = false;
            },
            value(val) {
                this.currentValue = val;
            }
        },
        computed:{
            placeholderMiddle: function () {
                if(!this.isPlaceholderMiddle)//不设置显示
                    return false;
                if(this.currentValue){//有值
                    return false;
                }else if(this.visible){
                    return false;
                }
                return true;
            }
        },
        methods:{
            placeholderMiddleClick(){
                this.visible = true;
                this.$nextTick(()=>{
                    this.$refs.input.focus();
                })

            }
        }
    }
</script>