<template>
  <div
    class="jc-grids">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'JCGrid',
  methods: {
    countColumn () {
      this.childrenSize = this.$children.length
      this.$children.forEach((c, index) => (c.index = index))
    }
  },
  props: {
    gridPadding: {
      type: String,
      default: '10px'
    },
    cols: {
      type: Number
    },
    showVerticalDividers: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    column () {
      return this.cols || this.childrenSize
    }
  },
  data () {
    return {
      childrenSize: 3
    }
  }
}
</script>
