<template>
  <div class="jc-form-item" :class="[
    sizeClass ? 'jc-form-item-' + sizeClass : ''
  ]">
    <label :for="labelFor" class="jc-form-item_label" v-bind:style="labelStyle" v-if="label || $slots.label">
      <slot name="label">{{label + form.labelSuffix}}</slot>
    </label>
    <div class="jc-form-item_content" v-bind:style="contentStyle">
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
    name: "JCFormItem",
    componentName: "JCFormItem",
    provide() {
        return {
            jcFormItem: this
        };
    },
    inject: ["jcForm"],
    props: {
        label: String,
        labelWidth: String,
        prop: String,
        for: String,
        size: String
    },
    computed: {
        labelFor() {
            return this.for || this.prop;
        },
        labelStyle() {
            const ret = {};
            if (this.form.labelPosition === "top") return ret;
            const labelWidth = this.labelWidth || this.form.labelWidth;
            if (labelWidth) {
                ret.width = labelWidth;
            }
            return ret;
        },
        contentStyle() {
            const ret = {};
            const label = this.label;
            if (this.form.labelPosition === "top" || this.form.inline)
                return ret;
            if (!label && !this.labelWidth && this.isNested) return ret;
            const labelWidth = this.labelWidth || this.form.labelWidth;
            if (labelWidth) {
                ret.marginLeft = labelWidth;
            }
            return ret;
        },
        form() {
            let _this = this;
            let parent = _this.$parent;
            let parentName = parent.$options.componentName;
            while (parentName !== "JCForm") {
                if (parentName === "JCFormItem") {
                    _this.isNested = true;
                }
                parent = parent.$parent;
                parentName = parent.$options.componentName;
            }
            return parent;
        },
        _formSize() {
            return this.jcForm.size;
        },
        jcFormItemSize() {
            return this.size || this._formSize;
        },
        sizeClass() {
            return this.jcFormItemSize;
        }
    },
    data() {
        return {
            isNested: false
        };
    }
};
</script>