<template>
  <a :class="$namespace +'-cell'" :href="href">
    <span class="jc-cell-mask" v-if="isLink"></span>
    <div class="jc-cell-left">
      <slot name="left"></slot>
    </div>
    <div class="jc-cell-wrapper">
      <div class="jc-cell-title">
        <slot name="icon">
          <i v-if="icon" :class="'jcm-' + icon"></i>
        </slot>
        <slot name="title">
          <span class="jc-cell-text" v-text="title"></span>
          <span v-if="label" class="jc-cell-label" v-text="label"></span>
        </slot>
      </div>
      <div class="jc-cell-value" :class="{ 'is-link' : isLink }">
        <slot>
          <span v-text="value"></span>
        </slot>
      </div>
      <i v-if="isLink" class="jc-arrows jcm-arrows"></i>
    </div>
    <div class="jc-cell-right">
      <slot name="right"></slot>
    </div>
  </a>
</template>
<script>
export default {
    name: "JCCell",
    props: {
        to: [String, Object],
        icon: String,
        title: String,
        label: String,
        isLink: Boolean,
        value: {}
    },
    computed: {
        href() {
            if (this.to && !this.added && this.$router) {
                const resolved = this.$router.match(this.to);
                if (!resolved.matched.length) return this.to;
                this.$nextTick(() => {
                    this.added = true;
                    this.$el.addEventListener("click", this.handleClick);
                });
                return resolved.fullPath || resolved.path;
            }
            return this.to;
        }
    },
    methods: {
        handleClick($event) {
            $event.preventDefault();
            this.$router.push(this.href);
        }
    }
};
</script>