<template>
    <table class="jc-table" :class="{'jc-table-bordered': fullBordered, 'jc-table-no-cell-bordered': !cellBordered, 'jc-table-no-content-bordered': !contentBordered,'jc-table-2':Sty2}">
        <slot>
            <thead>
                <tr>
                    <th>111</th>
                    <th>222</th>
                    <th>333</th>
                </tr>
            </thead>
        </slot>
    </table>
</template>

<script>
export default {
    //组件名称
    name: "JCTable",
    //组件将不会把未被注册的props呈现为普通的HTML属性
    inheritAttrs: false,
    props: {
        fullBordered: Boolean,
        cellBordered: {
            type: Boolean,
            default: true
        },
        contentBordered: {
            type: Boolean,
            default: true
        },
        Sty2: {
            type: Boolean,
            default: false
        }
    }
};
</script>
