import JCInput from '@/jcui/packages/input/src/input'
import JCHeader from '@/jcui/packages/header/src/header'
import JCButton from '@/jcui/packages/button/src/button'
import JCFlexbox from '@/jcui/packages/flexbox/src/flexbox'
import JCFlexboxItem from '@/jcui/packages/flexbox/src/flexbox-item'
import JCTable from '@/jcui/packages/table/src/table'
import JCCard from '@/jcui/packages/card/src/card'
import JCCell from '@/jcui/packages/cell/src/cell'
import JCContainer from '@/jcui/packages/container/src/container'
import JCNavbar from '@/jcui/packages/navbar/src/navbar'
import JCTabItem from '@/jcui/packages/tab-item/src/tab-item'
import JCTabContainer from '@/jcui/packages/tab-container/src/tab-container'
import JCTabContainerItem from '@/jcui/packages/tab-container-item/index'
import JCSelect from '@/jcui/packages/select/src/select'
import JCOption from '@/jcui/packages/select/src/option'
import JCTabbar from '@/jcui/packages/tabbar/src/tabbar'
import JCSwitch from '@/jcui/packages/switch/src/switch'
import JCForm from '@/jcui/packages/form/src/form'
import JCFormItem from '@/jcui/packages/form-item/index'
import JCGrid from '@/jcui/packages/grid/src/grid'
import JCGridItem from '@/jcui/packages/grid-item/index'
import JCSlider from '@/jcui/packages/slider/src/slider'
import JCProgress from '@/jcui/packages/progress/src/progress'
import JCSearch from '@/jcui/packages/search/src/search'
import JCActionsheet from '@/jcui/packages/actionsheet/src/actionsheet'
import JCRadio from '@/jcui/packages/radio/src/radio'
import JCChecklist from '@/jcui/packages/checklist/src/checklist'
import JCCommented from '@/jcui/packages/commented/src/commented'
import JCPicker from '@/jcui/packages/picker/src/picker'
import JCDatetimePicker from '@/jcui/packages/datetime-picker/src/datetime-picker'
import JCTreelist from '@/jcui/packages/treelist/src/treelist'
import JCPopup from '@/jcui/packages/popup/src/popup'
import JCKalendar from '@/jcui/packages/kalendar/src/kalendar'
import JCToast from '@/jcui/packages/toast/src/toast'

const components = {
    JCInput,
    JCHeader,
    JCButton,
    JCFlexbox,
    JCFlexboxItem,
    JCTable,
    JCCard,
    JCCell,
    JCContainer,
    JCNavbar,
    JCTabItem,
    JCTabContainer,
    JCTabContainerItem,
    JCSelect,
    JCOption,
    JCTabbar,
    JCSwitch,
    JCForm,
    JCFormItem,
    JCGrid,
    JCGridItem,
    JCSlider,
    JCProgress,
    JCSearch,
    JCActionsheet,
    JCRadio,
    JCChecklist,
    JCPicker,
    JCCommented,
    JCDatetimePicker,
    JCTreelist,
    JCPopup,
    JCKalendar
}

const version = '0.0.1';

const install = function(Vue) {

    if (install.installed) return;

    for (const item in components) {
        if (components[item].name) {
          Vue.component(components[item].name, components[item])
        }
      }

    //挂载vue插件
    //Vue.use(InfiniteScroll);

    //挂载vue全局实例对象
    Vue.$toast = Vue.prototype.$toast = JCToast;
};

//浏览器环境下自动安装插件
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

export default {
    install,
    version,
    JCInput,
    JCHeader,
    JCButton,
    JCFlexbox,
    JCFlexboxItem,
    JCTable,
    JCCard,
    JCCell,
    JCContainer,
    JCNavbar,
    JCTabItem,
    JCTabContainer,
    JCTabContainerItem,
    JCSelect,
    JCOption,
    JCTabbar,
    JCSwitch,
    JCForm,
    JCFormItem,
    JCGrid,
    JCGridItem,
    JCSlider,
    JCProgress,
    JCSearch,
    JCActionsheet,
    JCRadio,
    JCChecklist,
    JCPicker,
    JCCommented,
    JCDatetimePicker,
    JCTreelist,
    JCKalendar,
    JCToast
}