<template>
  <form class="jc-form" :class="[
    labelPosition ? 'jc-form-label-' + labelPosition : '',
    { 'jc-form-inline': inline },{'jc-form-style-2': Sty2}
  ]">
    <slot></slot>
  </form>
</template>
<script>
export default {
    name: "JCForm",
    componentName: "JCForm",
    provide() {
        return {
            jcForm: this
        };
    },
    props: {
        model: Object,
        labelPosition: String,
        labelWidth: String,
        labelSuffix: {
            type: String,
            default: ""
        },
        inline: Boolean,
        size: String,
        disabled: Boolean,
        Sty2: Boolean
    }
};
</script>