<template>
  <div :class="$namespace +'-slider'" class="is-vertical">
    <div class="slider-runway" :class="{ 'disabled': sliderDisabled,'slider-button-strip':square }" @click="onSliderClick" ref="slider">
      <div class="slider-bar" :style="barStyle">
      </div>
      <slider-button v-model="firstValue"  ref="button1">
      </slider-button>
      <!--第二个按钮，当range为true时（范围选择），显示。-->
      <slider-button v-model="secondValue" ref="button2" v-if="range">
      </slider-button>
      <div class="slider-stop" v-for="(item, key) in stops" :key="key" :style="{ 'left': item + '%' }" v-if="showStops">
      </div>
      <div class="minNamb">{{min}}</div>
      <div class="maxNamb">{{max}}</div>
    </div>
  </div>
</template>

<script type="text/babel">
  import SliderButton from './sliderbutton.vue';
  import Emitter from "@/jcui/mixins/emitter";
  export default {
    name: 'JCSlider',
    mixins:[Emitter],
    components: {
      SliderButton
    },
    props: {
      min: {//滑块左侧显示的最小值
        type: Number,
        default: 0
      },
      max: {//块右侧显示的最大值
        type: Number,
        default: 100
      },
      step: {//步长
        type: Number,
        default: 1
      },
      value: {
        type: [Number, Array],
        default: 0
      },
      showStops: {//是否显示间断点
        type: Boolean,
        default: false
      },
      showTooltip: {//是否显示当前数值
        type: Boolean,
        default: false
      },
      disabled: {//是否禁用
        type: Boolean,
        default: false
      },
      square:{//滑块是否为方形，默认圆形
          type:Boolean,
          default:false
      },
      range: {//是否为范围选择
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        firstValue: null,
        secondValue: null,
        oldValue: null,
        dragging: false,
        sliderSize: 1
      };
    },
    watch: {
      value(val, oldVal) {
        if (this.dragging ||
          Array.isArray(val) &&
          Array.isArray(oldVal) &&
          val.every((item, index) => item === oldVal[index])) {
          return;
        }
        this.setValues();
      },
      dragging(val) {
        if (!val) {
          this.setValues();
        }
      },
      firstValue(val) {
        if (this.range) {
          this.$emit('input', [this.minValue, this.maxValue]);
        } else {
          this.$emit('input', val);
        }
      },
      secondValue() {
        if (this.range) {
          this.$emit('input', [this.minValue, this.maxValue]);
        }
      },
      min() {
        this.setValues();
      },
      max() {
        this.setValues();
      }
    },
    methods: {
      valueChanged() {//滑块的值发生化
        if (this.range) {
          return ![this.minValue, this.maxValue]
            .every((item, index) => item === this.oldValue[index]);
        } else {
          return this.value !== this.oldValue;
        }
      },
      setValues() {
        if (this.min > this.max) {
          console.error('[Element Error][Slider]min should not be greater than max.');
          return;
        }
        const val = this.value;
        if (this.range && Array.isArray(val)) {
          if (val[1] < this.min) {
            this.$emit('input', [this.min, this.min]);
          } else if (val[0] > this.max) {
            this.$emit('input', [this.max, this.max]);
          } else if (val[0] < this.min) {
            this.$emit('input', [this.min, val[1]]);
          } else if (val[1] > this.max) {
            this.$emit('input', [val[0], this.max]);
          } else {
            this.firstValue = val[0];
            this.secondValue = val[1];
            if (this.valueChanged()) {
              this.dispatch('ElFormItem', 'el.form.change', [this.minValue, this.maxValue]);
              this.oldValue = val.slice();
            }
          }
        } else if (!this.range && typeof val === 'number' && !isNaN(val)) {
          if (val < this.min) {
            this.$emit('input', this.min);
          } else if (val > this.max) {
            this.$emit('input', this.max);
          } else {
            this.firstValue = val;
            if (this.valueChanged()) {
              this.dispatch('ElFormItem', 'el.form.change', val);
              this.oldValue = val;
            }
          }
        }
      },
      setPosition(percent) {//更新滑块按钮位置
        const targetValue = this.min + percent * (this.max - this.min) / 100;
        if (!this.range) {
          this.$refs.button1.setPosition(percent);
          return;
        }
        let button;
        if (Math.abs(this.minValue - targetValue) < Math.abs(this.maxValue - targetValue)) {
          button = this.firstValue < this.secondValue ? 'button1' : 'button2';
        } else {
          button = this.firstValue > this.secondValue ? 'button1' : 'button2';
        }
        this.$refs[button].setPosition(percent);
      },
      onSliderClick(event) {//点击滑块其它部位时，按钮位移
        if (this.sliderDisabled || this.dragging) return;
        this.resetSize();
        const sliderOffsetLeft = this.$refs.slider.getBoundingClientRect().left;
        this.setPosition((event.clientX - sliderOffsetLeft) / this.sliderSize * 100);
        this.emitChange();
      },
      resetSize() {
        if (this.$refs.slider) {
          this.sliderSize = this.$refs.slider[`client${ 'Width' }`];
        }
      },
      emitChange() {
        this.$nextTick(() => {
          this.$emit('change', this.range ? [this.minValue, this.maxValue] : this.value);
        });
      }
    },
    computed: {
      stops() {//显示间断点
        if (!this.showStops || this.min > this.max) return [];
        if (this.step === 0) {
          process.env.NODE_ENV !== 'production' &&
            console.warn('[Element Warn][Slider]step should not be 0.');
          return [];
        }
        const stopCount = (this.max - this.min) / this.step;
        const stepWidth = 100 * this.step / (this.max - this.min);
        const result = [];
        for (let i = 1; i < stopCount; i++) {
          result.push(i * stepWidth);
        }
        if (this.range) {
          return result.filter(step => {
            return step < 100 * (this.minValue - this.min) / (this.max - this.min) ||
              step > 100 * (this.maxValue - this.min) / (this.max - this.min);
          });
        } else {
          return result.filter(step => step > 100 * (this.firstValue - this.min) / (this.max - this.min));
        }
      },
      minValue() {
        return Math.min(this.firstValue, this.secondValue);
      },
      maxValue() {
        return Math.max(this.firstValue, this.secondValue);
      },
      barSize() {
        return this.range ?
          `${ 100 * (this.maxValue - this.minValue) / (this.max - this.min) }%` :
          `${ 100 * (this.firstValue - this.min) / (this.max - this.min) }%`;
      },
      barStart() {
        return this.range ?
          `${ 100 * (this.minValue - this.min) / (this.max - this.min) }%` :
          '0%';
      },
      precision() {
        let precisions = [this.min, this.max, this.step].map(item => {
          let decimal = ('' + item).split('.')[1];
          return decimal ? decimal.length : 0;
        });
        return Math.max.apply(null, precisions);
      },
      runwayStyle() {
        return {};
      },
      barStyle() {
        return {
          width: this.barSize,
          left: this.barStart
        }
      },
      sliderDisabled() {
        return this.disabled || (this.elForm || {}).disabled;
      }
    },
    mounted() {
      let valuetext;
      if (this.range) {
        if (Array.isArray(this.value)) {
          this.firstValue = Math.max(this.min, this.value[0]);
          this.secondValue = Math.min(this.max, this.value[1]);
        } else {
          this.firstValue = this.min;
          this.secondValue = this.max;
        }
        this.oldValue = [this.firstValue, this.secondValue];
        valuetext = `${this.firstValue}-${this.secondValue}`;
      } else {
        if (typeof this.value !== 'number' || isNaN(this.value)) {
          this.firstValue = this.min;
        } else {
          this.firstValue = Math.min(this.max, Math.max(this.min, this.value));
        }
        this.oldValue = this.firstValue;
        valuetext = this.firstValue;
      }
      this.resetSize();
    }
  };
</script>

