<template>
  <div class="jc-select-dropdown jc-popper" :class="[{ 'is-multiple': $parent.multiple }, popperClass]" :style="{ minWidth: minWidth }">
    <slot></slot>
  </div>
</template>

<script type="text/babel">
import Popper from "@/jcui/mixins/vue-popup";
export default {
    name: "JCSelectDropdown",
    componentName: "JCSelectDropdown",
    mixins: [Popper],
    props: {
        placement: {
            default: "bottom-start"
        },
        boundariesPadding: {
            default: 0
        },
        popperOptions: {
            default() {
                return {
                    gpuAcceleration: false
                };
            }
        },
        visibleArrow: {
            default: true
        },
        appendToBody: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            minWidth: ""
        };
    },
    computed: {
        popperClass() {
            return this.$parent.popperClass;
        }
    },
    watch: {
        "$parent.inputWidth"() {
            this.minWidth =
                this.$parent.$el.getBoundingClientRect().width + "px";
        }
    },
    mounted() {
        if (this.$parent.type == "input") {
            this.referenceElm = this.$parent.$refs.reference.$el;
        } else {
            this.referenceElm = this.$parent.$refs.refbtn;
        }
        this.$parent.popperElm = this.popperElm = this.$el;
        this.$on("updatePopper", () => {
            if (this.$parent.visible) this.$nextTick(this.updatePopper());
        });
        this.$on("destroyPopper", this.destroyPopper);
    }
};
</script>