<template>
    <div :class="$namespace +'-radio'" @change="$emit('change', currentValue)">
        <div class="radio-tit" v-if="title">{{title}}</div>
        <ul class="radio-ul">
            <li v-for="(item,index) in radioOptions" :key="index" :class="{'selected': getSelect(item)}">
                <label>
                  <input
                          class="radio-input"
                          type="radio"
                          v-model="currentValue"
                          :value="item.value || item"
                  >
                <span class="" v-text="item.label || item"></span>
                </label>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "JCRadio",
        props: {
            title: {//标题，显示在选项上方
                type: String,
                default: ''
            },
            radioOptions: {//选择项
                type: Array,
                default: () => []
            },
            value: String//绑定值
        },
        data() {
            return {
                currentValue: this.value
            }
        },
        watch: {
            value(val) {
                this.currentValue = val;
            },

            currentValue(val) {
                this.$emit('input', val);
            },
        },
        mounted(){
        },
        methods: {
            getSelect(item) {
                if (this.currentValue == item.value)
                    return true;
                return false;
            },
        }
    };
</script>