<template>
  <li @click.stop="selectOptionClick" class="jc-select-dropdown_item" v-show="visible" :class="{
      'selected': itemSelected,
      'is-disabled': disabled || groupDisabled,
    }">
    <slot>
      <span>{{ currentLabel }}</span>
    </slot>
  </li>
</template>

<script type="text/babel">
import Emitter from "@/jcui/mixins/emitter";
import { getValueByPath } from "@/jcui/mixins/util";
export default {
    mixins: [Emitter],
    name: "JCOption",
    componentName: "JCOption",
    inject: ["select"],
    props: {
        value: {
            required: true
        },
        label: [String, Number],
        created: Boolean,
        disabled: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            index: -1,
            groupDisabled: false,
            visible: true,
            hitState: false
        };
    },
    computed: {
        isObject() {
            return (
                Object.prototype.toString.call(this.value).toLowerCase() ===
                "[object object]"
            );
        },
        currentLabel() {
            return this.label || (this.isObject ? "" : this.value);
        },
        currentValue() {
            return this.value || this.label || "";
        },
        itemSelected() {
            return this.contains(this.select.value, this.value);
        }
    },
    watch: {
        currentLabel() {
            if (!this.created) this.dispatch("JCSelect", "setSelected");
        },
        value() {
            if (!this.created) this.dispatch("JCSelect", "setSelected");
        }
    },
    methods: {
        isEqual(a, b) {
            if (!this.isObject) {
                return a === b;
            } else {
                const valueKey = this.select.valueKey;
                return (
                    getValueByPath(a, valueKey) === getValueByPath(b, valueKey)
                );
            }
        },
        contains(arr = [], target) {
            if (!this.isObject) {
                return arr.indexOf(target) > -1;
            } else {
                const valueKey = this.select.valueKey;
                return arr.some(item => {
                    return (
                        getValueByPath(item, valueKey) ===
                        getValueByPath(target, valueKey)
                    );
                });
            }
        },
        handleGroupDisabled(val) {
            this.groupDisabled = val;
        },
        selectOptionClick() {
            if (this.disabled !== true && this.groupDisabled !== true) {
                this.dispatch("JCSelect", "handleOptionClick", [this, true]);
            }
        },
        queryChange(query) {
            // query 里如果有正则中的特殊字符，需要先将这些字符转义
            let parsedQuery = String(query).replace(
                /(\^|\(|\)|\[|\]|\$|\*|\+|\.|\?|\\|\{|\}|\|)/g,
                "\\$1"
            );
            this.visible =
                new RegExp(parsedQuery, "i").test(this.currentLabel) ||
                this.created;
        }
    },
    created() {
        this.select.options.push(this);
        this.select.cachedOptions.push(this);
        this.select.optionsCount++;
        this.$on("queryChange", this.queryChange);
        this.$on("handleGroupDisabled", this.handleGroupDisabled);
    }
};
</script>