<template>
    <div>
<!--        <transition name="actionsheet-float">
            <div v-show="visible" class="jc-actionsheet">
                <div class="actionsheet-tit" v-if="title">{{ title }}</div>
                <ul class="actionsheet-list" :style="{ 'margin-bottom': cancelText ? '5px' : '0' }">
                    <li v-for="( item, index ) in actions" class="actionsheet-listitem" @click.stop="itemClick(item, index)" :key="index">{{ item.name }}</li>
                </ul>
                <a class="actionsheet-button" @click.stop="visible = false" v-if="cancelText">{{ cancelText }}</a>
            </div>
        </transition>-->

        <JCPopup v-model="visible" position="bottom" style="width: 100%">
            <div class="jc-actionsheet">
                <div class="actionsheet-tit" v-if="title">{{ title }}</div>
                <ul class="actionsheet-list" :style="{ 'margin-bottom': cancelText ? '5px' : '0' }">
                    <li v-for="( item, index ) in actions" class="actionsheet-listitem" @click.stop="itemClick(item, index)" :key="index">{{ item.name }}</li>
                </ul>
                <a class="actionsheet-button" @click.stop="visible = false" v-if="cancelText">{{ cancelText }}</a>
            </div>
        </JCPopup>
    </div>
</template>
<script>
    export default {
        name: "JCActionsheet",
        props: {
            title:{//标题
                type: String,
                default:''
            },
            cancelText: {//取消按钮文字
                type: String,
                default: '取消'
            },
            actions: {//菜单项数组(元素内name为名称，method为点击执行的方法)
                type: Array,
                default: () => []
            },
            value:false,
        },
        data() {
            return {
                visible: this.value//是否显示
            };
        },
        watch: {
            visible(val) {
                this.$emit('input', val);
            },
            value(val) {
                this.visible = val;
            }
        },
        methods: {
            itemClick(item, index) {
                if (item.method && typeof item.method === 'function') {
                    item.method(item, index);
                }
                this.visible = false;
            }
        }
    }
</script>