<template>
  <div :class="[
    type === 'textarea' ? 'jc-textarea' : 'jc-input',
    inputSize ? 'jc-input-' + inputSize : '',
    {
      'is-disabled': inputDisabled,
      'jc-input-group': $slots.prepend || $slots.append,
      'jc-input-group-append': $slots.append,
      'jc-input-group-prepend': $slots.prepend,
      'jc-input-prefix': $slots.prefix || prefixIcon,
      'jc-input-suffix': $slots.suffix || suffixIcon || clearable
    }
    ]" @mouseenter="hovering = true" @mouseleave="hovering = false">
    <template v-if="type !== 'textarea'">
      <!-- 前置元素 -->
      <div class="jc-input-group_prepend" v-if="$slots.prepend">
        <slot name="prepend"></slot>
      </div>
      <input :tabindex="tabindex" v-if="type !== 'textarea'" :class="['jc-input_inner', inputRound?'jc-input-round':'']" v-bind="$attrs" :type="type" :disabled="inputDisabled" :autocomplete="autoComplete" :value="currentValue" ref="input" @input="handleInput" @focus="handleFocus" @blur="handleBlur" @change="handleChange" :aria-label="label">
      <!-- 前置内容 -->
      <span class="jc-input_prefix" v-if="$slots.prefix || prefixIcon" :style="prefixOffset">
        <slot name="prefix"></slot>
        <i class="jc-input_icon" v-if="prefixIcon" :class="prefixIcon">
        </i>
      </span>
      <!-- 后置内容 -->
      <span class="jc-input_suffix" v-if="$slots.suffix || suffixIcon || showClear" :style="suffixOffset">
        <span class="jc-input_suffix-inner">
          <template v-if="!showClear">
            <slot name="suffix"></slot>
            <i class="jc-input_icon" v-if="suffixIcon" :class="suffixIcon">
            </i>
          </template>
          <i v-else class="jc-input_icon jcm-guanbi jc-input_clear" @click="clear"></i>
        </span>
      </span>
      <!-- 后置元素 -->
      <div class="jc-input-group_append" v-if="$slots.append">
        <slot name="append"></slot>
      </div>
    </template>
    <textarea v-else :tabindex="tabindex" class="jc-textarea_inner" :value="currentValue" @input="handleInput" ref="textarea" v-bind="$attrs" :disabled="inputDisabled" :style="textareaStyle" @focus="handleFocus" @blur="handleBlur" @change="handleChange" :aria-label="label">
    </textarea>
  </div>
</template>
<script>
import emitter from "@/jcui/mixins/emitter";
import calcTextareaHeight from "./calcTextareaHeight";
export default {
    name: "JCInput",
    componentName: "JCInput",
    mixins: [emitter],
    //组件将不会把未被注册的props呈现为普通的HTML属性
    inheritAttrs: false,
    inject: {
        jcForm: {
            default: ""
        },
        jcFormItem: {
            default: ""
        }
    },
    data() {
        return {
            currentValue:
                this.value === undefined || this.value === null
                    ? ""
                    : this.value,
            textareaCalcStyle: {},
            prefixOffset: null,
            suffixOffset: null,
            hovering: false,
            focused: false,
            isOnComposition: false,
            valueBeforeComposition: null
        };
    },
    props: {
        value: [String, Number],
        size: String,
        round: Boolean,
        resize: String,
        form: String,
        disabled: Boolean,
        type: {
            type: String,
            default: "text"
        },
        autosize: {
            type: [Boolean, Object],
            default: false
        },
        autoComplete: {
            type: String,
            default: "off"
        },
        suffixIcon: String,
        prefixIcon: String,
        label: String,
        clearable: {
            type: Boolean,
            default: false
        },
        tabindex: String
    },
    computed: {
        _jcFormItemSize() {
            return (this.jcFormItem || {}).jcFormItemSize;
        },
        textareaStyle() {
            return Object.assign({}, this.textareaCalcStyle, {
                resize: this.resize
            });
        },
        inputSize() {
            return (
                this.size || this._jcFormItemSize
            );
        },
        inputRound() {
            return this.round;
        },
        inputDisabled() {
            return this.disabled || (this.jcForm || {}).disabled;
        },
        // isGroup() {
        //     return this.$slots.prepend || this.$slots.append;
        // },
        showClear() {
            return (
                this.clearable &&
                !this.disabled &&
                !this.readonly &&
                this.currentValue !== "" &&
                (this.focused || this.hovering)
            );
        }
    },
    watch: {
        value(val) {
            this.setCurrentValue(val);
        }
    },
    methods: {
        focus() {
            (this.$refs.input || this.$refs.textarea).focus();
        },
        blur() {
            (this.$refs.input || this.$refs.textarea).blur();
        },
        handleBlur(event) {
            this.focused = false;
            this.$emit("blur", event);
        },
        select() {
            (this.$refs.input || this.$refs.textarea).select();
        },
        resizeTextarea() {
            const { autosize, type } = this;
            if (type !== "textarea") return;
            if (!autosize) {
                this.textareaCalcStyle = {
                    minHeight: calcTextareaHeight(this.$refs.textarea).minHeight
                };
                return;
            }
            const minRows = autosize.minRows;
            const maxRows = autosize.maxRows;
            this.textareaCalcStyle = calcTextareaHeight(
                this.$refs.textarea,
                minRows,
                maxRows
            );
        },
        handleFocus(event) {
            this.focused = true;
            this.$emit("focus", event);
        },
        handleInput(event) {
            const value = event.target.value;
            this.setCurrentValue(value);
            this.$emit("input", value);
        },
        handleChange(event) {
            this.$emit("change", event.target.value);
        },
        setCurrentValue(value) {
            this.currentValue = value;
            this.$nextTick(this.resizeTextarea());
        },
        clear() {
            this.$emit("input", "");
            this.$emit("change", "");
            this.$emit("clear");
            this.setCurrentValue("");
            this.focus();
        }
    },
    created() {
        this.$on("inputSelect", this.select);
    },
    mounted() {
        this.resizeTextarea();
    }
};
</script>