<template>
  <div class="jc-tab-container">
    <div class="jc-tab-container-wrap">
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
    name: "JCTabContainer",
    props: {
        value: {}
    },
    data() {
        return {
            currentActive: this.value
        };
    },
    watch: {
        value(val) {
            this.currentActive = val;
        },
        currentActive(val) {
            this.$emit("input", val);
        }
    }
};
</script>