<template>
    <div :class="$namespace +'-switchbox'">
        <span  :value="value" @click="toggle"
               :class="['jc-switch-'+ type, checkedClass]"></span>
    </div>
</template>
<script>
    export default {
        name: "JCSwitch",
        props: {
            thin: Boolean,
            value: {
                type: Boolean,
                default: true
            },
            change: {
                type: Function,
                default: null
            }
        },
        data() {
            return {
                checked: this.value
            }
        },
        watch: {
            checked(val) {
                this.$emit('input', val);//更新控件value值
                this.$emit("change", val);//回调选中的值
            }
        },
        methods: {
            toggle() {
                this.checked = !this.checked;
            }
        },
        computed: {
            type(){//默认fat类型。
                return this.thin ? 'thin' :'fat';
            },
            checkedClass() {//选中样式
                return this.checked ? `jc-switchon-${ this.type }` : '';
            },
        }
    }
</script>