<template>
    <div class="jc-header">
        <div class="jc-header-left">
            <slot name="overwrite-left">
                <div class="jc-arrow jcm-fanhui" @click="onClickBack" v-show="_leftOptions.showBack"></div>
                <a class="jc-header-back" v-show="_leftOptions.showBack" @click="onClickBack">{{ typeof _leftOptions.backText === 'undefined' ? '返回' : _leftOptions.backText}}</a>
            </slot>
            <slot name="left"></slot>
        </div>
        <h1 class="jc-header-title" @click="$emit('on-click-title')" v-if="!shouldOverWriteTitle">
            <slot>
                <span v-show="title">{{title}}</span>
            </slot>
        </h1>
        <div class="jc-header-title-area" v-if="shouldOverWriteTitle">
            <slot name="overwrite-title"></slot>
        </div>
        <div class="jc-header-right">
            <a class="jc-header-more jcm-jiahao1" @click="$emit('on-click-more')" v-if="rightOptions.showMore"></a>
            <slot name="right"></slot>
        </div>
    </div>
</template>
<script>
    export default {
        name: "JCHeader",
        //组件将不会把未被注册的props呈现为普通的HTML属性
        inheritAttrs: false,
        componentName: "JCHeader",
        props: {
            leftOptions: Object,
            title: String,
            rightOptions: {
                type: Object,
                default () {
                    return {
                        showMore: false
                    }
                }
            }
        },
        beforeMount() {
            if (this.$slots["overwrite-title"]) {
                this.shouldOverWriteTitle = true;
            }
        },
        computed: {
            _leftOptions() {
                return Object.assign({
                        showBack: true,
                        preventGoBack: false
                    },
                    this.leftOptions || {}
                );
            }
        },
        methods: {
            onClickBack() {
                if (this._leftOptions.preventGoBack) {
                    this.$emit("on-click-back");
                } else {
                    this.$router ? this.$router.back() : window.history.back();
                }
            }
        },
        data() {
            return {
                shouldOverWriteTitle: false
            };
        }
    };
</script>

