<template>
    <div :class="$namespace +'-card'">
        <ul class="card-ui">
            <!--水平排多张图片样式-->
            <li class="transverse" v-if="horizontal">
                <a href="#">
                    <slot name="header"></slot>
                    <div class="bd">
                        <div class="cu-piclst" v-if="picList.length>0">
                            <span v-for="(imgSrc,index) in picList" :key="index">
                            <img :src="imgSrc" v-if="index < 3">
                          </span>
                        </div>
                        <div class="tr-detail cu-detail">
                            <h2 class="tit">{{title}}</h2>
                            <p class="resume">{{content}}</p>
                        </div>
                        <slot name="footer"></slot>
                    </div>
                </a>
            </li>
            <!--单张图片样式-->
            <li v-else>
                <a href="#">
                    <div class="cu-pic">
                        <img :src="pic">
                    </div>
                    <div class="cu-detail">
                        <h2 class="tit">{{title}}</h2>
                        <p class="resume">{{content}}</p>
                        <div class="tag" v-if="tag">
                            <p class="time">{{time}}</p>
                            <p class="source">{{source}}</p>
                        </div>
                    </div>
                </a>
            </li>
        </ul>
    </div>
</template>
<script>
    export default {
        name: "JCCard",
        props: {
            pic: {//图片
                type: String,
                default: "/mobile/cu-pic.jpg"
            },
            title: {//标题
                type: String,
                default: ''
            },
            content: {//内容
                type: String,
                default: ''
            },
            tag: {//是否显示时间与来源
                type: Boolean,
                default: true
            },
            time: {//时间
                type: String,
                default: ''
            },
            source: {//来源
                type: String,
                default: ''
            },
            picList: {//图片列表
                type: Array,
                default () {
                    return [];
                },
                validator: function(value) {
                    return value.length <= 3;
                }
            },
            horizontal: {//水平排多张图片
                type: Boolean,
                default: false
            }
        }
    };
</script>

