<template>
  <div @change="$emit('change', currentValue)" class="jc-checklist">
    <div class="checklist-title">
      <label class="checklist-label" v-if="haveCheckAll">
            <span class="checkbox">
                <input type="checkbox" class="checkbox-input" v-model="checkAll" @change="handleCheckAllChange">
                <span class="checkbox-core"></span>
            </span>
            <span class="checkbox-label">全选</span>
            <span class="btn-del" @click.prevent="done">完成</span>
      </label>
    </div>
    <div v-for="(option,index) in options" :key="index">
      <label class="checklist-label">
          <span
            :class="{'is-right': align === 'right'}"
            class="checkbox">
            <input
              class="checkbox-input"
              type="checkbox"
              v-model="currentValue"
              :value="option.value || option"
              @change="handleCheckedChange">
            <span class="checkbox-core"></span>
          </span>
          <span class="checkbox-label" v-text="option.label || option"></span>
        </label>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'JCChecklist',
    props: {
      haveCheckAll:Boolean,//是否显示全选按钮
      title: String,//标题
      align: String,//复选框对其位置（左，右）
      options: {//选择项
        type: Array,
        required: true
      },
      value: Array
    },
    data() {
      return {
        currentValue: this.value,
        checkAll:false
      }
    },
    watch: {
      value(val) {
        this.currentValue = val;
      },
      currentValue(val) {
        this.$emit('input', val);
      }
    },
    methods: {
      done() {
        this.$router ? this.$router.back() : window.history.back();
      },
      handleCheckAllChange(){//点击全选按钮时
          this.currentValue = [];
          if(this.checkAll){
              this.options.forEach((option)=>{
                  this.currentValue.push(option.value||option);
              })
          }
      },
      handleCheckedChange(){//点击各个选项时，同步全选按钮选中状态
          let checkedLength = this.currentValue.length;
          this.checkAll = checkedLength==this.options.length;
      }
    }
  };
</script>