<template>
  <div class="jc-select" :class="[selectSize ? 'jc-select-' + selectSize : '']" @click.stop="toggleMenu" v-clickoutside="handleClose">
    <JCInput v-if="type == 'input'" ref="reference" v-model="selectedLabel" type="text" :placeholder="currentPlaceholder" :name="name" :id="id" :auto-complete="autoComplete" :size="selectSize" :readonly="readonly" :class="{ 'is-focus': visible }" @focus="handleFocus" @blur="handleBlur" @keyup.native="onInputChange" @keydown.native.down.stop.prevent="navigateOptions('next')" @keydown.native.up.stop.prevent="navigateOptions('prev')" @keydown.native.enter.prevent="selectOption" @keydown.native.esc.stop.prevent="visible = false" @keydown.native.tab="visible = false" @paste.native="onInputChange">
      <template slot="prefix" v-if="$slots.prefix">
        <slot name="prefix"></slot>
      </template>
      <i slot="suffix" :class="['jc-select_caret', 'jc-input_icon', 'jcm-' + iconClass]"></i>
    </JCInput>
    <div ref="refbtn" v-if="type !== 'input'">
      <slot name="button"></slot>
    </div>
    <JCSelectMenu ref="popper" :append-to-body="popperAppendToBody" v-show="visible">
      <ul :class="['jc-select-dropdown_wrap', 'jc-select-dropdown_list']" v-show="options.length > 0 ">
        <JCOption :value="query" created v-if="showNewOption">
        </JCOption>
        <slot></slot>
      </ul>
    </JCSelectMenu>
  </div>
</template>
<script type="text/babel">
import JCSelectMenu from "./select-dropdown.vue";
import Emitter from "@/jcui/mixins/emitter";
import Focus from "@/jcui/mixins/focus";
import JCOption from "@/jcui/packages/select/src/option";
import { addClass, removeClass, hasClass } from "@/jcui/mixins/dom";
import { getValueByPath } from "@/jcui/mixins/util";
import { valueEquals } from "@/jcui/mixins/util";
import NavigationMixin from "@/jcui/packages/select/src/navigation-mixin";
import scrollIntoView from "@/jcui/mixins/scroll-into-view";
import Clickoutside from "@/jcui/mixins/clickoutside";

export default {
    mixins: [Emitter, Focus("reference"), NavigationMixin],
    name: "JCSelect",
    componentName: "JCSelect",
    inject: {
        jcForm: {
            default: ""
        },
        jcFormItem: {
            default: ""
        }
    },
    provide() {
        return {
            select: this
        };
    },
    computed: {
        _jcFormItemSize() {
            return (this.jcFormItem || {}).jcFormItemSize;
        },
        readonly() {
            const isIE =
                !this.$isServer && !isNaN(Number(document.documentMode));
            return !isIE && !this.visible;
        },
        iconClass() {
            let criteria =
                this.value !== undefined &&
                this.value !== null &&
                this.value !== "";
            return criteria ? "circle-close is-show-close" : "xiajiantou";
        },
        showNewOption() {
            let hasExistingOption = this.options
                .filter(option => !option.created)
                .some(option => option.currentLabel === this.query);
            return this.query !== "" && !hasExistingOption;
        },
        selectSize() {
            return (
                this.size || this._elFormItemSize
            );
        }
    },
    components: {
        JCOption,
        JCSelectMenu
    },
    directives: { Clickoutside },
    props: {
        name: String,
        id: String,
        type: {
            type: String,
            default: "input"
        },
        value: {
            required: true
        },
        autoComplete: {
            type: String,
            default: "off"
        },
        automaticDropdown: Boolean,
        size: String,
        popperClass: String,
        placeholder: {
            type: String,
            default() {
                return "请选择";
            }
        },
        valueKey: {
            type: String,
            default: "value"
        },
        popperAppendToBody: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            options: [],
            cachedOptions: [],
            selected: {},
            inputLength: 20,
            inputWidth: 0,
            cachedPlaceHolder: "",
            visible: false,
            softFocus: false,
            selectedLabel: "",
            query: "",
            previousQuery: null,
            currentPlaceholder: "",
            isSilentBlur: false
        };
    },
    watch: {
        placeholder(val) {
            this.cachedPlaceHolder = this.currentPlaceholder = val;
        },
        value(val, oldVal) {
            this.setSelected();
            if (!valueEquals(val, oldVal)) {
                this.dispatch("JCFormItem", "jc.form.change", val);
            }
        },
        visible(val) {
            if (!val) {
                this.handleIconHide();
                this.broadcast("JCSelectDropdown", "destroyPopper");
                if (this.$refs.input) {
                    this.$refs.input.blur();
                }
                this.query = "";
                this.previousQuery = null;
                this.selectedLabel = "";
                this.inputLength = 20;
                this.$nextTick(() => {
                    if (
                        this.$refs.input &&
                        this.$refs.input.value === "" &&
                        this.selected.length === 0
                    ) {
                        this.currentPlaceholder = this.cachedPlaceHolder;
                    }
                });
                if (this.selected) {
                    this.selectedLabel = this.selected.currentLabel;
                }
            } else {
                this.handleIconShow();
                this.broadcast("JCSelectDropdown", "updatePopper");
            }
            this.$emit("visible-change", val);
        },
        options() {
            this.$nextTick(() => {
                this.broadcast("JCSelectDropdown", "updatePopper");
            });
            let inputs = this.$el.querySelectorAll("input");
            if ([].indexOf.call(inputs, document.activeElement) === -1) {
                this.setSelected();
            }
        }
    },
    methods: {
        handleQueryChange(val) {
            if (this.previousQuery === null) {
                this.previousQuery = val;
                return;
            }
            this.previousQuery = val;
            this.$nextTick(() => {
                if (this.visible)
                    this.broadcast("JCSelectDropdown", "updatePopper");
            });
            this.broadcast("JCOption", "queryChange", val);
            this.broadcast("JCOptionGroup", "queryChange");
        },
        handleIconHide() {
            let icon = this.$el.querySelector(".jc-input_icon");
            if (icon) {
                removeClass(icon, "is-reverse");
            }
        },
        handleIconShow() {
            let icon = this.$el.querySelector(".jc-input_icon");
            if (icon && !hasClass(icon, "jcm-guanbi")) {
                addClass(icon, "is-reverse");
            }
        },
        scrollToOption(option) {
            const target =
                Array.isArray(option) && option[0] ? option[0].$el : option.$el;
            if (this.$refs.popper && target) {
                const menu = this.$refs.popper.$el.querySelector(
                    ".jc-select-dropdown_wrap"
                );
                scrollIntoView(menu, target);
            }
            this.$refs.scrollbar && this.$refs.scrollbar.handleScroll();
        },
        emitChange(val) {
            if (!valueEquals(this.value, val)) {
                this.$emit("change", val);
            }
        },
        getOption(value) {
            let option;
            const isObject =
                Object.prototype.toString.call(value).toLowerCase() ===
                "[object object]";
            const isNull =
                Object.prototype.toString.call(value).toLowerCase() ===
                "[object null]";
            for (let i = this.cachedOptions.length - 1; i >= 0; i--) {
                const cachedOption = this.cachedOptions[i];
                const isEqual = isObject
                    ? getValueByPath(cachedOption.value, this.valueKey) ===
                      getValueByPath(value, this.valueKey)
                    : cachedOption.value === value;
                if (isEqual) {
                    option = cachedOption;
                    break;
                }
            }
            if (option) return option;
            const label = !isObject && !isNull ? value : "";
            let newOption = {
                value: value,
                currentLabel: label
            };
            return newOption;
        },
        setSelected() {
            this.selected = this.getOption(this.value);
        },
        handleFocus(event) {
            if (!this.softFocus) {
                if (this.automaticDropdown) {
                    this.visible = true;
                    this.menuVisibleOnFocus = true;
                }
                this.$emit("focus", event);
            } else {
                this.softFocus = false;
            }
        },
        blur() {
            this.visible = false;
            this.$refs.reference.blur();
        },
        handleBlur(event) {
            setTimeout(() => {
                if (this.isSilentBlur) {
                    this.isSilentBlur = false;
                } else {
                    this.$emit("blur", event);
                }
            }, 50);
            this.softFocus = false;
        },
        handleClose() {
            this.visible = false;
        },
        handleOptionSelect(option, byClick) {
            if (this.type == "input") {
                this.$emit("input", option.value);
                this.emitChange(option.value);
                this.visible = false;
                this.isSilentBlur = byClick;
                this.setSoftFocus();
                if (this.visible) return;
                this.$nextTick(() => {
                    this.scrollToOption(option);
                });
            } else {
                // console.log(option)
            }
        },
        setSoftFocus() {
            this.softFocus = true;
            const input = this.$refs.input || this.$refs.reference;
            if (input) {
                input.focus();
            }
        },
        toggleMenu() {
            this.visible = !this.visible;
        },
        selectOption() {
            if (!this.visible) {
                this.toggleMenu();
            }
        },
        onInputChange() {
            if (this.query !== this.selectedLabel) {
                this.query = this.selectedLabel;
                this.handleQueryChange(this.query);
            }
        }
    },
    created() {
        this.cachedPlaceHolder = this.currentPlaceholder = this.placeholder;
        this.onInputChange();
        this.$on("handleOptionClick", this.handleOptionSelect);
        this.$on("setSelected", this.setSelected);
    },
    mounted() {
        this.$nextTick(() => {
            if (this.$refs.reference && this.$refs.reference.$el) {
                this.inputWidth = this.$refs.reference.$el.getBoundingClientRect().width;
            }
        });
        this.setSelected();
    }
};
</script>