<template>
    <div class="jc-container" :class="{'jc-container-pad': conPadding}">
        <slot></slot>
    </div>
</template>
<script>
export default {
    //组件名称
    name: "JCContainer",
    //组件将不会把未被注册的props呈现为普通的HTML属性
    inheritAttrs: false,
    props: {
        conPadding: {
            type: Boolean
        }
    }
};
</script>
