<template>
  <button class="jc-button" @click="handleClick" :disabled="buttonDisabled || loading" :autofocus="autofocus" :type="nativeType" :class="[    
      buttonSize ? 'jc-button-' + buttonSize : '',
      circle ? 'jc-button-circle':'',
      type ? 'jc-button-' + type : '',
      {
        'is-disabled': buttonDisabled,
        'is-loading': loading,
        'is-plain': plain,
        'is-round': round,
        'is-circle': circle,
        'is-full': full
      }
    ]">
    <i class="jcm-loading" v-if="loading"></i>
    <i :class="icon" v-if="icon && !loading"></i>
    <span v-if="$slots.default">
      <slot></slot>
    </span>
  </button>
</template>
<script>
export default {
    name: "JCButton",
    //组件将不会把未被注册的props呈现为普通的HTML属性
    inheritAttrs: false,
    inject: {
        jcForm: {
            default: ""
        },
        jcFormItem: {
            default: ""
        }
    },
    props: {
        type: {
            type: String,
            default: "default"
        },
        size: String,
        icon: {
            type: String,
            default: ""
        },
        nativeType: {
            type: String,
            default: "button"
        },
        loading: Boolean,
        disabled: Boolean,
        plain: Boolean,
        autofocus: Boolean,
        round: Boolean,
        circle: Boolean,
        full: Boolean
    },
    computed: {
        _jcFormItemSize() {
            return (this.jcFormItem || {}).jcFormItemSize;
        },
        buttonSize() {
            return (
                this.size || this._jcFormItemSize
            );
        },
        buttonDisabled() {
            return this.disabled || (this.jcForm || {}).disabled;
        }
    },
    methods: {
        handleClick(evt) {
            this.$emit("click", evt);
        }
    }
};
</script>